  text-transform: uppercase;
  color: #000000;
}

.header__nav .nav__link:focus, .header__nav .nav__link:hover, .header__nav .nav__link:active {
  text-decoration: none;
  color: #BF0000;
  transition: all 0.2s ease;
}

.header__mobile--nav {
  display: none;
}

.menu-active {
  display: block;
  transition: all 0.5s ease;
  width: 40px;
}

.menu-burger__header {
